// Mobile first
@mixin desktop {
  @media (min-width: 1024px) {
    @content;
  }
}

@mixin tablet-mf {
  @media (min-width: 768px) {
    @content;
  }
}

@mixin desktop-lg {
  @media (min-width: 1240px) {
    @content;
  }
}

@mixin retina {
  @media (min-resolution: 2dppx) {
    @content;
  }
}

// Desktop first
@mixin desktop-sm {
  @media (max-width: 1047px) {
    @content;
  }
}

@mixin tablet-df {
  @media (max-width: 1023px) {
    @content;
  }
}

@mixin mobile {
  @media (max-width: 767px) {
    @content;
  }
}

///////

@mixin xl {
  @media (min-width: 1250px) {
    @content;
  }
}


@mixin lg {
  @media (max-width: 1249px) {
    @content;
  }
}

@mixin md {
  @media (max-width: 991px) {
    @content;
  }
}

@mixin sm {
  @media (max-width: 767px) {
    @content;
  }
}

@mixin xs {
  @media (max-width: 575px) {
    @content;
  }
}